button {
    min-height: 40px;
    min-width: 40px;
    display: flex;
    padding: 4px;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    cursor: pointer;
    gap: 8px;

    &.delete {
        color: #EF3059;
    }

    &.primary {
        color: var(--color4)
    }
}